.entrWrapper {
    background-color: #0e1015de;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin: auto;
    color: white;
    position: relative;
    padding: 5%;
    width: 90%;
    border-radius: 20px;overflow: hidden;
}

.entImageWrapper {
    position: absolute;
    height: 100%;
    width: 100%;
    justify-content: flex-end;
    align-items: center;
    margin: auto;
    display: flex;
    padding-right: 5%;
}

.entImageWrapper img {
    height: 100%;
    width: auto;
    display: flex;
    object-fit: contain;
}

.entWriteUp {
    flex-direction: column;
    display: flex;
    width: 100%;
    gap: 30px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.writeupWrapper {
    width: 70%;
    gap: 20px;
    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.entWriteUp h1 span {
    background: linear-gradient(to right, lightgreen, teal);
    /* Adjust colors as needed */
    -webkit-background-clip: text;
    /* For Safari */
    background-clip: text;
    color: transparent;
}

.sponsors {
    width: 100%;
    display: flex;
    height: auto;
    gap: 20px;
    justify-content: space-evenly;
}

.sponsorWrapper {
    height: 100%;
    width: auto;
    display: flex;
}

.sponsorWrapper img {
    object-fit: contain;
    display: flex;
    height: auto;
    width: 100%;
}

@media screen and (max-width:900px) {
    .entrWrapper {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin: auto;
        color: white;
        position: relative;
        padding: 5%;
        width: 98%;
        border-radius: 5px;
    }

    .entImageWrapper {
        position: absolute;
        height: 100%;
        width: 100%;
        justify-content: flex-end;
        align-items: center;
        margin: auto;
        display: flex;
        padding: unset;
    }

    .entImageWrapper img {
        height: 100%;
        width: 100%;
        display: flex;
        object-fit: contain;
    }

    .entWriteUp {
        flex-direction: column;
        display: flex;
        width: 100%;
        gap: 30px;
        font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }

    .writeupWrapper {
        width: 100%;
        gap: 20px;
        flex: 1;
        height: 100%;
        display: flex;
        flex-direction: column;
    }
}