.forex-main-wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.forex-section-wrapper {
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 70%;
    height: auto;
    margin: auto;
}

.forex-widget-wrapper {
    width: 100%;
    height: auto;
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
}

.forex-image-wrapper {
    width: 100%;
    margin: auto;
    height: 300px;
    display: flex;
    overflow: hidden;
    align-items: flex-start;
    justify-content: flex-start;
    position: relative;
    border-radius: 20px;
}

.forex-image-wrapper img {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    top: 0px;
    object-fit: cover;
}

@media screen and (max-width:900px) {
    .forex-section-wrapper {
        display: flex;
        flex-direction: column;
        gap: 20px;
        width: 90%;
        height: auto;
        margin: auto;
    }
}