.heroImage {
    height: auto;
    width: 100%;
    flex: 1;
    justify-content: center;
    overflow: hidden;
    position: relative;
    border-radius: 0px 0px 20px 20px;
    overflow: hidden;

}

.heroContent {
    display: flex;
    height: 500px;
    width: auto;
    position: relative;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 0px 0px 15px 15px;
    overflow: hidden;
}

.heroImageWrapper {
    height: 100%;
    width: 100%;
    position: absolute;
    justify-content: center;
    align-items: center;
    margin: auto;
    top: 0px;
}

.stockMarketImage {
    height: 100%;
    width: 100%;
    display: flex;
    object-fit: cover;
    margin: auto;
    position: absolute;
    top: 0px;
}

.overlayDiv {
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    position: relative;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.596),
            rgba(0, 0, 0, 0.637), rgb(0, 8, 8));
}

.heroWriteUp {
    display: flex;
    margin: auto;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    color: white;
    width: 70%;
    position: relative;
    gap: 20px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.heroWriteUp h2 {
    display: flex;
    font-size: 40px;
    font-weight: bold;
    text-transform: uppercase;
}

.heroWriteUp p {
    font-size: 12px;
    font-weight: bold;
    width: 70%;
    height: auto;
}

.heroAuthButton {
    width: 100%;
    height: auto;
    justify-content: center;
    align-items: center;
    display: flex;
    gap: 50px;
    padding: 10px 0px;
}

.heroAuthBtn {
    /* background-color: green; */
    padding: 10px 0px;
    width: 150px;
    text-decoration: none;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    position: relative;
    overflow: hidden;
    border: 1px solid lightseagreen;

    font-size: 12px;
}

.heroAuthBtn::before {
    height: 100%;
    width: 0%;
    content: "";
    position: absolute;
    background: linear-gradient(to bottom right, teal, lightgreen);
    left: 0px;
    transition: 500ms all ease;
    border-radius: 2px;
}

.heroAuthBtn:hover:before {
    width: 100%;
}

.heroAuthBtn h3 {
    z-index: 1;
    font-family: montserrat, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

@media screen and (max-width: 900px) {

    .heroContent {
        display: flex;
        height: 400px;
        width: auto;
        position: relative;
        justify-content: center;
        align-items: flex-end;
        border-radius: 0px 0px 20px 20px;
        overflow: hidden;
    }

    .heroWriteUp {
        display: flex;
        margin: unset;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        color: white;
        gap: 20px;
        width: 90%;
        height: auto;
        position: relative;
        padding-bottom: 20px;
        font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }

    .heroWriteUp h2 {
        display: flex;
        font-size: 15px;
        font-weight: bold;
        text-transform: uppercase;
    }

    .heroWriteUp p {
        font-size: 12px;
        font-weight: bold;
        width: 100%;
        height: auto;
    }
}