.overview-main-container {
    width: 100%;
    height: 100%;
    padding: 0px;
    display: flex;
    overflow: hidden;
}

.overview-widget-wrapper {
    flex: 1;
    width: 100%;
    height: 100%;
    border: none;
    overflow: hidden;
}

@media screen and (max-width:900px) {
    .overview-main-container {
        width: 100%;
        height: 100%;
        padding: 0px;
        display: flex;
        overflow: unset;
    }
}