.subaboutwrapper {
    background-color: #0E1015;
    height: auto;
    width: 100%;
    display: flex;
    position: relative;
    overflow: hidden;
    /* padding: 5% 10%; */
    position: relative;
    align-items: center;
    justify-content: center;
}

.subaboutimageWrapper {
    height: auto;
    width: 100%;
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    position: relative;
}

.subwriteup {
    /* background-color: red; */
    position: relative;
    height: auto;
    margin: auto;
    width: 70%;
    z-index: 1;
    flex: 1;
    padding: 5%;
}

.subwriteup h2 {
    color: white;
    font-size: 15px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-align: center;
    /* text-transform: uppercase; */
    position: relative;
    margin: auto;
    width: 70%;
}


.backgroundtradewallpaer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: "auto";
    width: 100%;
    position: absolute;
    height: inherit;
    /* bottom: 0px; */
}

.backgroundtradewallpaer img {
    width: 100%;
    height: 100%;
    display: flex;
    margin: auto;
    object-fit: cover;
}

.getStarted {
    justify-content: center;
    align-items: center;
    height: auto;
    display: flex;
    margin-top: 50px;
}

.getStartedBtn {
    height: 100%;
    justify-content: flex-start;
    align-items: flex-start;
    display: flex;
    margin: auto;
    border: 2px solid transparent;
    text-decoration: none;
    color: white;
    position: relative;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    transition: 500ms all ease;

    border-image: linear-gradient(to right, lightgreen, teal);
    border-image-slice: 1;
}

.getStartedBtn h3 {
    padding: 10px 30px;
    color: white;
}

.getStartedBtn:hover {
    color: white;
    font-weight: bold;
}

.getStartedBtn::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 0%;
    margin: auto;
    display: flex;
    z-index: -1;
    background: linear-gradient(to right, lightgreen, teal);
    transition: 500ms all ease;
}

.getStartedBtn:hover::before {
    width: 100%;
}

.subAbout-getStarted {
    color: black;
}

.backgroundtradewallpaer.wb-subabout {
    display: flex;
}

.backgroundtradewallpaer .mb-subabout {
    display: none;
}

@media screen and (max-width:900px) {
    .subaboutwrapper {
        height: auto;
        width: 100%;
        display: flex;
        position: relative;
        overflow: hidden;
        /* padding: 5% 10%; */
        position: relative;
        align-items: center;
        justify-content: center;
    }

    .subwriteup {
        width: 70%;
        margin: auto;
    }

    .subwriteup h2 {
        display: flex;
        color: white;
        text-align: left;
        font-size: 15px;
        font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        position: relative;
        margin: auto;
        width: 100%;
    }

    .backgroundtradewallpaer .wb-subabout {
        display: none;
    }

    .backgroundtradewallpaer {
        height: auto;
        width: 100%;
    }

    .backgroundtradewallpaer .mb-subabout {
        display: flex;
        height: 100%;
        width: 100%;
    }
}